'use strict';

import getColorPalette from './color-thief';
var PALETTESIZE = 4;
export default function (img, debug) {
  var canvas = document.createElement('canvas');
  var w = img.naturalWidth;
  var h = img.naturalHeight;
  canvas.width = w;
  canvas.height = h;
  var context = canvas.getContext('2d');
  context.drawImage(img, 0, 0, w, h);
  var tl = getColorPalette(context.getImageData(0, 0, w / 2, h / 2).data, PALETTESIZE);
  var tr = getColorPalette(context.getImageData(w / 2, 0, w / 2, h / 2).data, PALETTESIZE);
  var bl = getColorPalette(context.getImageData(0, h / 2, w / 2, h / 2).data, PALETTESIZE);
  var br = getColorPalette(context.getImageData(w / 2, h / 2, w / 2, h / 2).data, PALETTESIZE);
  var palette = getColorPalette(context.getImageData(0, 0, w, h).data, PALETTESIZE).map(toHex);
  var background = palette[0];
  var distances = [getDistance(tl[0], bl[0]), getDistance(tl[0], tr[0]), getDistance(tl[0], br[0]), getDistance(bl[0], tr[0]), getDistance(tr[0], br[0]), getDistance(bl[0], br[0])];
  var colors = [[tl[0], bl[0]], [tl[0], tr[0]], [tl[0], br[0]], [bl[0], tr[0]], [tr[0], br[0]], [bl[0], br[0]]];
  var directions = ['to bottom', 'to right', '135deg', '45deg', 'to bottom', 'to right'];
  var furthest = Math.max.apply(null, distances);
  var idx = distances.findIndex(function (el, idx) {
    return el === furthest;
  });
  var gradient = "linear-gradient(".concat(directions[idx], ", ").concat(toHex(colors[idx][0]), " 0%, ").concat(toHex(colors[idx][1]), " 100%)");

  if (debug) {
    var res = [];
    res.push(background);
    res.push(gradient);
    var pretty = "linear-gradient(\n  ".concat(directions[idx], ",\n  ").concat(toHex(colors[idx][0]), " 0%,\n  ").concat(toHex(colors[idx][1]), " 100%\n);");
    return {
      css: res,
      palettes: {
        topleft: tl.map(toHex),
        topright: tr.map(toHex),
        bottomleft: bl.map(toHex),
        bottomright: br.map(toHex),
        overall: palette
      },
      pretty: pretty
    };
  }

  var css = "background: ".concat(background, "; background: ").concat(gradient);
  return {
    css: css,
    background: background,
    gradient: gradient
  };
}

function toHex(rgb) {
  var r = Number(rgb[0]).toString(16);
  var g = Number(rgb[1]).toString(16);
  var b = Number(rgb[2]).toString(16);
  if (r.length === 1) r = '0' + r;
  if (g.length === 1) g = '0' + g;
  if (b.length === 1) b = '0' + b;
  return '#' + r + g + b;
}

function getDistance(rgb1, rgb2) {
  return Math.sqrt(Math.pow(rgb2[0] - rgb1[0], 2), Math.pow(rgb2[1] - rgb1[1], 2), Math.pow(rgb2[2] - rgb1[2], 2));
}